.navbar {
    width: 100%;
    background: linear-gradient(90deg, #007bff 0%, #1abc9c 100%);
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 32px;
    height: 64px;
    box-shadow: 0 4px 16px rgba(44,62,80,0.10);
    position: sticky;
    top: 0;
    z-index: 100;
    transition: background 0.3s;
}

.navbar-title {
    font-size: 1.7em;
    font-weight: bold;
    letter-spacing: 1px;
    text-shadow: 0 2px 8px rgba(0,0,0,0.08);
}

.navbar-links {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
    align-items: center;
}

.navbar-links li {
    margin-left: 32px;
    position: relative;
}

.navbar-links a {
    color: white;
    text-decoration: none;
    font-size: 1.08em;
    padding: 8px 0;
    border-bottom: 2px solid transparent;
    transition: border-color 0.2s, color 0.2s;
    position: relative;
}

.navbar-links a.active,
.navbar-links a:hover {
    color: #fff;
    border-bottom: 2px solid #fff;
}

@media (max-width: 900px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
        height: auto;
        padding: 12px;
    }
    .navbar-title {
        margin-bottom: 8px;
        font-size: 1.2em;
    }
    .navbar-links {
        flex-direction: column;
        width: 100%;
    }
    .navbar-links li {
        margin: 8px 0 0 0;
    }
}

@media (max-width: 600px) {
    .navbar {
        flex-direction: column;
        align-items: stretch;
        padding: 8px;
        height: auto;
    }
    .navbar-links {
        flex-direction: row;
        flex-wrap: wrap;
        overflow-x: auto;
        width: 100%;
        padding-bottom: 4px;
    }
    .navbar-links li {
        margin: 6px 12px 0 0;
    }
}