.calendar {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 10px;
    margin: 20px;
}

.calendar-header {
    grid-column: span 7;
    text-align: center;
    font-weight: bold;
    font-size: 1.5em;
}

.calendar-day {
    border: 1px solid #ccc;
    padding: 10px;
    min-height: 100px;
    position: relative;
}

.calendar-day:hover {
    background-color: #f0f0f0;
}

.event {
    background-color: #007bff;
    color: white;
    padding: 5px;
    border-radius: 3px;
    margin: 2px 0;
    font-size: 0.9em;
}

.today {
    border: 2px solid #007bff;
    background-color: #e7f1ff;
}

.calendar-weekday {
    background: #f4f4f4;
    font-weight: bold;
    text-align: center;
    color: #007bff;
}

@media (max-width: 600px) {
    .calendar {
        gap: 3px;
        margin: 4px;
    }
    .calendar-day {
        min-height: 40px;
        padding: 3px;
        font-size: 11px;
    }
    .calendar-header {
        font-size: 1em;
    }
}