.project-management-dashboard/src/styles/Projects.css

.projects-container {
    padding: 20px;
    background-color: #f9f9f9;
    overflow-x: auto;
}

.project-card {
    background: white;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    padding: 15px;
    margin-bottom: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s;
}

.project-card:hover {
    transform: scale(1.02);
}

.project-title {
    font-size: 1.5em;
    font-weight: bold;
    color: #333;
}

.project-deadline {
    font-size: 0.9em;
    color: #888;
}

.project-status {
    display: inline-block;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 0.8em;
    color: white;
}

.status-active {
    background-color: #28a745;
}

.status-completed {
    background-color: #007bff;
}

.status-pending {
    background-color: #ffc107;
}

.progress-bar {
    width: 100%;
    background-color: #e0e0e0;
    border-radius: 5px;
    overflow: hidden;
    margin-top: 10px;
}

.progress {
    height: 10px;
    background-color: #007bff;
    transition: width 0.4s ease;
}

.view-details {
    margin-top: 10px;
    text-align: right;
}

.view-details a {
    color: #007bff;
    text-decoration: none;
}

.view-details a:hover {
    text-decoration: underline;
}

.projects-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    min-width: 600px;
}

.projects-table th, .projects-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.projects-table th {
    background-color: #f2f2f2;
    color: #333;
}

/* Example for all tables */
.projects-container, .tasks-container, .team-container {
    overflow-x: auto;
}

.projects-table, .tasks-table, .team-table {
    min-width: 400px;
    width: 100%;
    font-size: 1em;
}

@media (max-width: 600px) {
    .projects-table, .tasks-table, .team-table {
        font-size: 0.95em;
        min-width: 320px;
    }
}

@media (max-width: 768px) {
    .projects-table {
        min-width: 400px;
        font-size: 14px;
    }
}