.task-list {
    list-style-type: none;
    padding: 0;
}

.task-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
    transition: background-color 0.3s;
}

.task-item:hover {
    background-color: #f9f9f9;
}

.task-status {
    font-weight: bold;
}

.task-priority {
    color: #ff9800; /* Orange for priority */
}

.task-completed {
    text-decoration: line-through;
    color: #a9a9a9; /* Gray for completed tasks */
}

.task-actions {
    display: flex;
    gap: 10px;
}

.button {
    padding: 5px 10px;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

.button-edit {
    background-color: #2196F3; /* Blue for edit */
    color: white;
}

.button-delete {
    background-color: #f44336; /* Red for delete */
    color: white;
}

.projects-container, .tasks-container, .team-container {
    overflow-x: auto;
}

.projects-table, .tasks-table, .team-table {
    min-width: 400px;
    width: 100%;
    font-size: 1em;
}

@media (max-width: 600px) {
    .projects-table, .tasks-table, .team-table {
        font-size: 0.95em;
        min-width: 320px;
    }
}

@media (max-width: 768px) {
    .task-item {
        flex-direction: column;
        align-items: flex-start;
    }

    .task-actions {
        width: 100%;
        justify-content: space-between;
    }

    .tasks-table {
        font-size: 13px;
        min-width: 350px;
    }
}